<template>
  <div>
    <div :style="'background-image: linear-gradient(0deg, '+view.colors.ambient+', '+view.colors.lighter+')'" id="view">
    </div>
    <div v-if="!view.isLoading">
      <tooltip/>
      <div id="fullscreen" @click="setFullScreen()" v-show="!getBrowser().isSafari" v-popover:tooltip.left="'Set fullscreen mode'">
        <icon class="white" :name="view.isFullScreen ? 'compress': 'expand'" scale="2"></icon>
      </div>
    </div>
    <div v-if="view.isLoading">
      <div class="loaders">
        <div class="loader circle-loader"></div>
      </div>
    </div>
  </div>
</template>

<script>
import UtilService from "../services/util";
import InitService from "../services/init";
import EventService from "../services/event";
import CreatorService from "../services/creator";
import AnimatorService from "../services/animator";
import MusicService from "../services/music";

require("animate.css");

export default {
  name: "Vitae",
  mixins: [
    UtilService,
    InitService,
    EventService,
    CreatorService,
    AnimatorService,
    MusicService
  ],
  data() {
    var darker = "#0F2027";
    var lighter = "#2C5364";
    var moreLighter = this.colorLuminance(lighter, 0.5);

    return {
      view: {
        camera: null,
        scene: null,
        renderer: null,
        controls: null,
        raycaster: new THREE.Raycaster(),
        mouse: new THREE.Vector2(),
        animations: {
          pulseUp: true,
          pulseDown: false,
          cameraIsExecuted: false
        },
        components: {
          particles: {},
          rocks: {},
          bulb: {},
          grid: {},
          songDetail: {},
          songs: [],
          cover: {},
          lights: [],
          ambientLight: {}
        },
        colors: {
          ambient: darker,
          directional: 0xbbbbbb,
          bulb: {
            external: 0xffffff,
            internal: moreLighter
          },
          particle: 0xffffff,
          rocks: moreLighter,
          text: 0xffffff,
          lighter: lighter
        },
        font: null,
        isMobile: this.isMobile(),
        isFullScreen: false,
        isVR: false,
        isLoading: true
      },
      music: {
        audio: {
          context: null,
          source: "static/soul.mp3",
          player: null,
          analyser: null,
          status: null,
          ready: false,
          fftSize: 1024
        },
        title: "VITÆ",
        artist: "Edoardo Spadoni",
        position: {
          x: 0,
          y: -4,
          z: -2
        }
      }
    };
  },
  mounted() {
    this.initView();
  },
  methods: {
    initView() {
      var context = this;

      // add WebGLRenderer
      this.initRenderer();

      // append renderer to view HTML element
      document
        .getElementById("view")
        .appendChild(this.view.renderer.domElement);

      // init the scene
      this.initScene();

      // init camera and add to scene
      this.initCamera({
        y: 1
      });

      // load font
      var loader = new THREE.FontLoader();
      loader.load("static/fonts/open-sans.json", function(font) {
        // add font
        context.view.font = font;

        // init all objects
        context.initAmbient(context);

        // init main bulb
        context.addBulb(
          false,
          false,
          {
            x: 0,
            y: -2,
            z: -2
          },
          context.view.colors.bulb.internal,
          context.view.colors.bulb.external,
          3,
          1,
          100,
          10
        );

        // init songs
        context.addSongDetail(
          "1. In Alto Loco",
          "",
          context.view.colors.text,
          {
            x: 0,
            y: 11,
            z: -2
          },
          {
            title: 0.5
          },
          1
        );
        context.addSongDetail(
          "2. Zahira",
          "",
          context.view.colors.text,
          {
            x: 0,
            y: 9.5,
            z: -2
          },
          {
            title: 0.5
          },
          2
        );
        context.addSongDetail(
          "3. Alter Ego",
          "",
          context.view.colors.text,
          {
            x: 0,
            y: 8,
            z: -2
          },
          {
            title: 0.5
          },
          3
        );
        context.addSongDetail(
          "4. Libra",
          "",
          context.view.colors.text,
          {
            x: 0,
            y: 6.5,
            z: -2
          },
          {
            title: 0.5
          },
          4
        );
        context.addSongDetail(
          "5. Memento Mori",
          "",
          context.view.colors.text,
          {
            x: 0,
            y: 5,
            z: -2
          },
          {
            title: 0.5
          },
          5
        );

        // add album cover
        //context.addAlbumCover();

        // init controls
        context.initControls();

        // init events
        context.initEvents();

        // start animations
        context.animate();

        // terminate loading
        context.view.isLoading = false;
      });
    },

    animate(dt) {
      // request update
      requestAnimationFrame(this.animate);

      // update renderer
      this.updateRenderer();

      // update controls
      this.view.controls.update();

      // animate camera
      if (!this.view.animations.cameraIsExecuted) {
        this.animateCamera();
      }

      // animate bulb
      this.animateBulb(false, "pulse", null, 0.13, 0.00025);
    }
  }
};
</script>

<style scoped>
body {
  background: #0f2027;
}

#view {
  background-size: 100%;
  text-align: center;
  margin: 0px;
  overflow: hidden;
}

#fullscreen {
  position: absolute;
  top: 5px;
  right: 13px;
  cursor: pointer;
}
</style>


